  describe("withRetry function", () => {
    test("executes operation successfully on first try", async () => {
      const mockOperation = jest.fn().mockResolvedValue("success");

      const result = await withRetry(mockOperation);

      expect(result).toBe("success");
      expect(mockOperation).toHaveBeenCalledTimes(1);
    });

    test("retries on failure and succeeds", async () => {
      const mockOperation = jest
        .fn()
        .mockRejectedValueOnce(new Error("First failure"))
        .mockResolvedValueOnce("success");

      const result = await withRetry(mockOperation, 2, 100);

      expect(result).toBe("success");
      expect(mockOperation).toHaveBeenCalledTimes(2);
      expect(setTimeout).toHaveBeenCalledWith(expect.any(Function), 100);
    });

    test("throws after max retries", async () => {
      const mockOperation = jest
        .fn()
        .mockRejectedValue(new Error("Persistent failure"));

      await expect(withRetry(mockOperation, 2, 100)).rejects.toThrow(
        "Persistent failure",
      );
      expect(mockOperation).toHaveBeenCalledTimes(3); // Initial + 2 retries
    });

    test("handles non-Error exceptions", async () => {
      const mockOperation = jest.fn().mockRejectedValue("String error");

      await expect(withRetry(mockOperation, 1, 100)).rejects.toThrow(
        "String error",
      );
      expect(mockOperation).toHaveBeenCalledTimes(2);
    });

    test("uses exponential backoff", async () => {
      const mockOperation = jest
        .fn()
        .mockRejectedValueOnce(new Error("Fail 1"))
        .mockRejectedValueOnce(new Error("Fail 2"))
        .mockResolvedValueOnce("success");

      await withRetry(mockOperation, 3, 100);

      expect(setTimeout).toHaveBeenNthCalledWith(1, expect.any(Function), 100);
      expect(setTimeout).toHaveBeenNthCalledWith(2, expect.any(Function), 200);
    });

    test("handles operation with no retries", async () => {
      const mockOperation = jest.fn().mockRejectedValue(new Error("Fail"));

      await expect(withRetry(mockOperation, 0, 100)).rejects.toThrow("Fail");
      expect(mockOperation).toHaveBeenCalledTimes(1);
    });
  });

